import { BaseRecord } from '../baserecord';

/**
 * Description on the Vehicle class : the fields listed below all result from vehicleService.get service.
 */
export class Vehicle extends BaseRecord{
  code: string;
  label: string;
  licence_id: string;
  vehicle_cat_id: string;
  empty_weight: number;
  driver_id: string;
  gcwr:number;
  beacon_major: number;
  site_id: string;
  driver_last_name: string;
  driver_first_name: string;
  status_label: string;
  fleet_label: string;
  fleet_id: string;
  vehicle_cat_label: string;
  lng: number;
  lat: number;
  dt: number;
  heading: number;
  speed:number;
}

/**
 * Description on the VehicleCat class.
 */
export class VehicleCat extends BaseRecord {
	code : string;
  label: string;
}