import { BaseRecord } from '../baserecord';
import { Site } from '../site/site';

/**
 * Description on the Person class : the fields listed below all result from personService.get service.
 */
export class Person extends BaseRecord{
    lastname: string;
    lastnamemaiden: string;
    firstname: string;
    gender_th: string;
    gender_code: string;
    gender_label: string;
    identifiantldap: string;
    birthday_dt: number;
    status_th: string;
    status_code: string;
    status_label: string;
    type_th: string;
    type_code: string;
    type_label: string;
    home:Site
    establishments:Site[];

  constructor() {
    super()
    this.establishments=[];
    this.home = new Site()
  }
}



