import { BaseRecord } from '../baserecord';

export enum RestResult {
  Error = 0,
  Ok = 1,
	DuplicateCode = 2,
}

export class CrudResult{
	result: RestResult;
	data: Object;
}

export class CrudGetResult{
	result: RestResult;
	data: BaseRecord;
}

export class CrudSaveResult{
	result: RestResult;
	data: BaseRecord;
}

export class CrudListResult{
	result: RestResult;
	data: BaseRecord[];
}