import { Directive, ElementRef,Input,HostListener ,AfterViewInit  } from '@angular/core';

@Directive({
  selector: '[fillHeight]'
})
export class FillHeight implements AfterViewInit  {

  @Input("fillHeight") top: string;

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.el.nativeElement.style.height = window.innerHeight-this.el.nativeElement.getBoundingClientRect().top-20 + "px";
  }

  constructor(private el: ElementRef) {
  }

  ngAfterViewInit () {    
    var top;
    if(this.top==""){
      top = this.el.nativeElement.getBoundingClientRect().top;
    }else{
      top = Number(this.top);
    }
    this.el.nativeElement.style.height = window.innerHeight - top - 20 + "px";
  }
}