import {Component, OnInit } from '@angular/core';
import { Router,ActivatedRoute } from '@angular/router';

import { BaseCrud } from '../basecrud';

import { Vehicle,VehicleCat } from './vehicle';
import { VehicleService } from './vehicle.service';

import { ThesaurusService } from '../thesaurus/thesaurus.service';

@Component({
  selector: 'app-vehicle-crud',
  templateUrl: './vehicle.crud.html',
  styleUrls: ['./vehicle.css']
})
export class VehicleCrud extends BaseCrud implements OnInit  {

	vehicleCats : VehicleCat[];

  constructor(
		private route: ActivatedRoute,
		protected router: Router,
  	private vehicleService:VehicleService,
    private thService:ThesaurusService,
    ) {
  		// Inject data service - it will be used by parent BaseCrud class
  		// to run CRUD actions
  		// It populates currentRecord member variable  		
      super(vehicleService,thService,router);
  	}

  ngOnInit() {
		// Load Vehicle
		this.route.params.subscribe(routeParams => {
			super.init(routeParams.id);
		});

		// Load Vehicle Categories
		this.vehicleService.listCat().subscribe(vehicleCats => this.vehicleCats = vehicleCats);
  }

}
