import { Injectable } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor } from '@angular/common/http';
import {Router} from '@angular/router';

import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { AuthenticationService } from '../login/authentication.service';
import { AlertService } from '../alert/alert.service';

@Injectable()
export class ErrorInterceptor implements HttpInterceptor {
  constructor(private authenticationService: AuthenticationService,
              private alertservice: AlertService,
              private router: Router) {}

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    return next.handle(request).pipe(catchError(err => {
      switch(err.status){
        case 401:
          // auto logout if 401 response returned from api
          this.authenticationService.logout();
          this.alertservice.error("Veuillez vous connecter.");
          this.router.navigateByUrl('/login');          
          break;
        case 403:
          this.alertservice.error("Vous n'avez pas accs  la ressource demande.");
          break;
        case 500:
          switch(err.error.errorCode){
            // See BaseObject class on server side for the declaration of custom codes
            case 101:
              this.alertservice.error("La rfrence fournie est dj utilise. Merci d'en choisir une autre.");
              break;
            case 102:
              this.alertservice.error("L'enregistrement des donnes a chou. Votre administrateur a t notifi.");
              break;              
            default:
              this.alertservice.error("Une erreur interne est survenue. Votre administrateur a t notifi."); 
              break;
          }
          break;
        default:
          break;  
      }      
      const error = err.error.message || err.statusText;
      return throwError(error);
    }));
  }
}