import { BaseRecord } from '../baserecord';
import { Point } from 'geojson';

/**
 * Description on the POI class : the fields listed below all result from poiService.get service.
 */
export class POI extends BaseRecord{
  site_main_id: string;
  position: number;
  label: string;
  addr1: string;
  addr2: string;
  postcode :string;
  city :string;
  country_th :string;
  type_th :string;
  geom: Point;
}
