import {Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

import { BaseCrud } from '../basecrud';
import { Person } from './person';

import { POI } from '../poi/poi';
import { POIModal } from '../poi/poi-modal';
import { PersonEstablishmentsModal } from './person.establishments-modal';
import { AOI } from '../aoi/aoi';
import { PersonService } from './person.service';
import { ThesaurusService } from '../thesaurus/thesaurus.service';
import { AlertService } from '../alert/alert.service';

@Component({
  selector: 'app-person-crud',
  templateUrl: './person.crud.html',
  styleUrls: ['./person.css']
})
export class PersonCrud extends BaseCrud implements OnInit  {

  // override type defined in parent class so that we can access currentRecord fields from within that class
  currentRecord : Person;

  // Incremented after any changes in the list of POIs received from host component
  changeInPOIs: number;

  // Incremented after any changes in the list of AOIs received from host component
  changeInAOIs: number;

  // Tell whether there are some changes in the formular that are not handled by form.pristine
  //  (for instance POI or AOI creation or deletion)
  bChanges:boolean;

  constructor(
  	private route: ActivatedRoute,
    protected router: Router,
    private personService:PersonService,
    private thService: ThesaurusService,
    private alertService: AlertService,
    private modalService: NgbModal) {
  		// Inject data service - it will be used by parent BaseCrud class
  		// to run CRUD actions
  		// It populates currentRecord member variable
      super(personService,thService,router);
      this.changeInPOIs = 0;
      this.changeInAOIs = 0;
      this.bChanges=false;
      // In case some data is loaded or reloaded
      this.dataLoaded.subscribe((currentRecord) => {
        // After data loading, no changes have occured
        this.bChanges=false;
        // Force the display of POIs and AOIs on the map
        this.changeInPOIs++;
        this.changeInAOIs++;

      });
  }

  /**
   * Called after DOM completion. It will request data from server
   */
  ngOnInit() {
    // Load Person
		this.route.params.subscribe(routeParams => {
			super.init(routeParams.id);
    });
  }

  /**
   * Triggered by map-leaflet Component after a marker drawing event on the map
   */
  public newPOI(event){
    var poi = { geom:event.value.geometry } as POI;
    this.currentRecord.home.POIs.push(poi);
    this.editPOI(poi,this.currentRecord.home.POIs.length-1);
  }

  /**
   * Remove a POI from the list of POIs and also from the map
   */
  protected editPOI(poi: POI, i: number){
    const modalRef = this.modalService.open(POIModal);
    (modalRef.componentInstance as POIModal).poi = poi;

    modalRef.result.then((result) => {
      if(result=="delete"){
        this.currentRecord.home.POIs.splice(i,1);
      }
    }).catch((error) => {
      console.log(error);
    });

    // Trigger the change detection on map so that POIs display can be updated on map
    this.changeInPOIs++;
    this.bChanges=true;
  }

  /**
   * Triggered by map-leaflet Component after a polygon drawing event on the map
   */
  public newAOI(event){
    this.currentRecord.home.AOIs.push({ geom:event.value.geometry } as AOI);
    this.bChanges=true;
  }

  /**
   * Remove a POI from the list of POIs and also from the map
   * @param index : index of the POI
   */
  protected deletePOI(index:number){
    this.currentRecord.home.POIs.splice(index,1);
    // Trigger the change detection on map so that POIs display can be updated on map
    this.changeInPOIs++;
    this.bChanges=true;
  }

  /**
   * Remove a AOI from the list of AOIs and also from the map
   * @param index : index of the AOI
   */
  protected deleteAOI(index:number){
    this.currentRecord.home.AOIs.splice(index,1);
    // Trigger the change detection on map so that AOIs display can be updated on map
    this.changeInAOIs++;
    this.bChanges=true;
  }

  /**
   * Remove an establishement from the list of establishments
   * @param index : index of the establihsment
   */
  protected deleteEstablishment(index:number){
    this.currentRecord.establishments.splice(index,1);
    this.bChanges=true;
  }

  /**
   * open the modal for adding a new establishment to the list of establishement
   */
  protected addEstablishment(){
    const modalRef = this.modalService.open(PersonEstablishmentsModal, {size: 'lg'});
    (modalRef.componentInstance as PersonEstablishmentsModal).establishments = this.currentRecord.establishments;

    modalRef.result.then((result) => {
    }).catch((error) => {
      console.log(error);
    });

    // Trigger the change detection on map so that POIs display can be updated on map
    this.changeInPOIs++;
    this.bChanges=true;
  }
}
