import { Component, OnInit,Input } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import {GridOptions} from "ag-grid-community";

import { BaseCrud } from '../basecrud';

import { UserService } from '../user/user.service';
import { ThesaurusService } from '../thesaurus/thesaurus.service';

import { User } from '../user/user';
import { AclRole } from './acl-role';

@Component({
  templateUrl: './acl-user-modal.crud.html',
})
export class ACLUserModalCrud extends BaseCrud implements OnInit {	
	
	@Input('aclRoles') aclRoles: AclRole[];
	@Input('userId') userId: string;

	private gridOptions: GridOptions;

	constructor(private userService:UserService,
  			  	    private thService:ThesaurusService,
			           protected router: Router,
			         public activeModal: NgbActiveModal) {
		super(userService,thService,router);
		this.currentRecord = new User();
		this.gridOptions = <GridOptions>{
								rowHeight:30,
                headerHeight:30,};

		this.gridOptions.columnDefs = [
	    {headerName: 'Rle', field: 'code',checkboxSelection: true },
	    {headerName: 'Description', field: 'label' },
		]; 
	}

	ngOnInit() {	
		super.init(this.userId);
	}

	gridReady(params){
		this.dataLoaded.subscribe((currentRecord) => {
	    this.gridOptions.api.forEachNode(node=> {
	      var selected = false;
	      for(let role of (<User>this.currentRecord).roles){
	        if(node.data.code == role.code){
	          selected = true;
	          break;
	        }
	      }
	      node.setSelected(selected);
	    });
	    params.api.sizeColumnsToFit();
	  });
	}

	saveModal() {
		(<User>this.currentRecord).roles = this.gridOptions.api.getSelectedRows();
		console.log("save",(<User>this.currentRecord).roles);

	  this.save(false).subscribe((record) => {
		this.activeModal.close('Modal Closed');
	  });	  
	}
}