import { Component,Input,Output,EventEmitter } from '@angular/core';

import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  templateUrl: './crud-navbar-modal.confirm-delete.html',
})
export class CrudNavbarModalConfirmDelete {	

  // Used to check ACL right to update, delete or mark as removed
  @Input('aclObject') aclObject: string;

	@Output() onCkeck = new EventEmitter();

	constructor(public activeModal: NgbActiveModal) {}

	onCheckDeleteChange(event){
		this.onCkeck.emit(event.currentTarget.checked);
	}
}