import { Component,Input } from '@angular/core';

import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import {GridOptions} from "ag-grid-community";

import { SiteService } from '../site/site.service';
import { Site } from '../site/site';

@Component({
  templateUrl: './person.establishments-modal.html',
})
export class PersonEstablishmentsModal {	

    // The list of establishments send by modal parent component
    // It also serves as a data output
    @Input('establishements') establishments: Site[];
    
    private gridOptions: GridOptions;

    // The list of new establishments. Same as site but with an address field that aggregates all the address subfields
    private otherEstablishments: {
        id:string;
        code:string;
        label:string;
        address:string;
        type_label:string;
        status_label:string;
        site_main_label_entity:string;
    }[];

    constructor(private siteService: SiteService, public activeModal: NgbActiveModal) {                    
      this.siteService.list({
          typeCode:"ETABLISHMENT",statusCode:"ENABLED",search:"",startIndex:0,length:0}).subscribe((sites) => {
        this.otherEstablishments=[];
        for(let site of sites){
          this.insertSite(site);
        }
      });    
      this.gridOptions = { 
        rowHeight:30,
        headerHeight:30,
        columnDefs : [
          {headerName: 'Rfrence', field: 'code', checkboxSelection: true},
          {headerName: 'Description', field: 'label'},
          {headerName: 'Adresse', field: 'address' },
        ]
      } as GridOptions;                  
	}

    /**
     * Called when grid is ready
     * @param params sent by the grid
     */
    gridReady(params){
	  params.api.sizeColumnsToFit();
	}

    /**
     * Insert a site in this.otherEstablishments only if the site does not belong to this.establishments.
     * @param site a site to be inserted into this.otherEstablishments
     */
    public insertSite(site){
      // Make sure that already selected establishments will not appear in the list
      // TODO : je pense que pour de gros volumes de donnes il faudrait faire ce filtrage ct serveur
      var bAlreadyInList = false;
      for(let establishment of this.establishments){
        if(establishment.code==site.code){
          bAlreadyInList = true;
          break;
        }
      }
      if(!bAlreadyInList){           
        this.otherEstablishments.push({
          id:site.id,
          code:site.code,
          label:site.label,
          address:this.formatAddress(site),
          type_label:site.type_label,
          status_label:site.status_label,
          site_main_label_entity:site.site_main_label_entity
        })
      }        
    }
    
    /**
     * Compute an address for a site by concatenating the site address subfields
     * @param site a site for which we need to compute the address in one field
     * @return string : the address for the site
     */
    public formatAddress(site){
      var address = "";
      if(site.addr1!=null){
        address+= site.addr1
      }
      if(site.addr2!=null){
        if(address!=''){
          address+=' - ';
        }
        address+= site.addr2
      }
      if(site.postcode!=null){
        if(address!=''){
          address+=' ';
        }
        address+= site.postcode
      }
      if(site.city!=null){
        if(address!=''){
          address+=' ';
        }
        address+= site.city
      }        
      return address;
    }

    /**
     * Triggered on "Valider" button click : it will update this.establishments with selected items from
     *   this.otherEstablishments
     */
	saveModal() {
	  this.gridOptions.api.forEachNode(node=> {
        if(node.isSelected()){
          this.establishments.push(node.data as Site)
        }  
      });        
	  this.activeModal.close("save");
	}
}