import { Directive, ElementRef,Input,Renderer,OnInit } from '@angular/core';

import { AclActionService } from './acl-action.service';
import { CrudResult } from '../helpers/crud-result';

@Directive({
  selector: '[aclShow]'
})
export class AclShowDirective implements OnInit {

  @Input("aclShow") action: string;

  constructor(private el: ElementRef,
              private renderer: Renderer,
              private aclService: AclActionService) {
  }

  ngOnInit() {
    let currentUser = JSON.parse(localStorage.getItem('currentUser'));
    this.aclService.userHasAccess(currentUser.user_main_id,this.action).subscribe( (response: CrudResult) => {
      if(response.data){
        this.renderer.setElementStyle(this.el.nativeElement, 'display', 'inline');
      }else{
        this.renderer.setElementStyle(this.el.nativeElement, 'display', 'none');
      }
    });
  }
}